.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  background-color: #fff;
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
}

.header svg {
  display: none;
}

.header p {
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  font-size: 36px;
  margin: 12px 0;
  color: navajowhite;
  letter-spacing: 1px;
}

.header ul {
  display: flex;
  margin: 0;
  padding: 0;
}

.header ul li {
  list-style: none;
}

.header ul li a {
  text-decoration: none;
  background-image: linear-gradient(180deg, #000, #ccc);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 900;
  padding: 10px 20px;
  font-family: "Poppins", sans-serif;
  font-size: 20px;
}

.header ul li a:hover {
  border-bottom: 2px solid #000;
}

@media screen and (max-width: 768px) {
  .header p {
    font-size: 30px;
  }

  .header ul li a {
    font-size: 18px;
  }
}

@media screen and (max-width: 600px) {
  .header ul {
    display: none;
  }

  .header svg {
    display: block;
    font-size: 26px;
    color: #1c1c1c;
    cursor: pointer;
  }
}

@media screen and (max-width: 600px) {
  .header p {
    font-size: 24px;
  }

  .header svg {
    font-size: 24px;
  }
}
