.about {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px auto;
  width: 85%;
  padding: 50px 0;
}

.about img {
  width: 450px;
  border-radius: 50% 50% 50% 50% / 20% 20% 80% 80%;
}

.about-info {
  width: min(100% - 2rem, 600px);
  margin-left: 50px;
}

.about-info h2 {
  margin: 15px 0;
  font-family: "Poppins", sans-serif;
  letter-spacing: 1px;
  font-size: 38px;
  font-weight: 900;
  color: transparent;
  -webkit-text-stroke: 2px #e35d6a;
}

.about-info p {
  color: white;
  font-family: "Poppins", sans-serif;
  line-height: 1.5;
  font-size: 17px;
  text-align: justify;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
}

.dwl-cv {
  text-decoration: none;
}

.dwl-cv button {
  background-color: #e35d6a;
  padding: 10px 20px;
  font-size: 24px;
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  border: 2px solid #fff;
  outline: none;
  border-radius: 5px;
}

.dwl-cv button:hover {
  background-color: transparent;
  color: navajowhite;
  border: 2px solid #fff;
  cursor: pointer;
  transition: 0.3s;
}

@media screen and (max-width: 992px) {
  .about {
    flex-direction: column;
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 0;
  }

  .about img {
    width: 350px;
    margin-bottom: 20px;
  }

  .about-info {
    margin-left: 0;
  }

  /* .about-info h2 {
    text-align: center;
  }

  .dwl-cv button {
    display: block;
    margin-inline: auto;
  } */
}

@media screen and (max-width: 450px) {
  .about {
    padding-bottom: 0;
  }

  .about img {
    width: 250px;
  }

  .about-info h2 {
    text-align: center;
    font-size: 34px;
  }

  .dwl-cv button {
    display: block;
    margin-inline: auto;
  }
}

@media screen and (max-width: 300px) {
  .about img {
    width: 200px;
  }

  .about-info h2 {
    font-size: 30px;
  }

  .dwl-cv button {
    font-size: 20px;
    padding: 5px 10px;
  }
}
