.footer {
  color: #fff;
  text-align: center;
  font-family: "Poppins", sans-serif;
  background-color: #1c1c1c;
  padding: 15px 0;
}

.footer-email {
  display: inline-flex;
  align-items: center;
}

.footer a:visited,
.footer a:active,
.footer a:link {
  color: #fff;
}

.footer a:hover {
  color: navajowhite;
}

.footer svg {
  font-size: 30px;
  margin: 0 10px;
}

.copyright {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 0;
}

@media screen and (max-width: 450px) {
  .footer svg {
    font-size: 22px;
    margin: 0 5px;
  }

  .footer-email span {
    font-size: 14px;
  }

  .copyright {
    font-size: 18px;
  }
}

@media screen and (max-width: 300px) {
  .footer svg {
    font-size: 18px;
    margin: 0 5px;
  }

  .footer-email span {
    font-size: 12px;
  }

  .copyright {
    font-size: 16px;
  }
}
