.projects {
  margin-inline: auto;
  padding: 100px 0;
  overflow-x: hidden;
}

.projects h1 {
  font-family: "Poppins", sans-serif;
  color: transparent;
  -webkit-text-stroke: 2px #e35d6a;
  font-size: 38px;
  font-weight: 900;
  letter-spacing: 2px;
  text-align: center;
  margin: 30px 0;
}

.projects-info {
  display: grid;
  grid-template-columns: 550px 550px;
  grid-template-rows: auto;
  justify-content: center;
  align-content: center;
  row-gap: 50px;
  column-gap: 50px;
}

.projects-info div {
  position: relative;
}

.projects-info div img {
  height: 350px;
  width: 100%;
  opacity: 0.95;
}

.overlay {
  position: absolute !important;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: #ffdeadd9;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.overlay h2 {
  font-family: "Poppins", sans-serif;
  color: #444;
  font-weight: 900;
  margin: 0;
  font-size: 40px;
  text-align: center;
}

.projects-info div:hover .overlay {
  opacity: 1;
}

.overlay p {
  text-align: center;
  font-family: "Poppins", sans-serif;
  color: #444;
}

.overlay p span {
  font-size: 18px;
  font-weight: 600;
}

.projects-btns {
  width: 70%;
  display: flex;
  justify-content: space-around;
}

.projects-btns a {
  text-decoration: none;
}

.projects-btns a:visited,
.projects-btns a:active,
.projects-btns a:link,
.projects-btns a:hover {
  color: #444;
}
.projects-btns button {
  font-family: "Oswald", sans-serif;
  font-weight: 700;
  font-size: 20px;
  background-color: #fff;
  color: #444;
  padding: 5px 10px;
  border: 2px solid transparent;
  border-radius: 5px;
}

.projects-btns button:hover {
  background-color: transparent;
  border: 2px solid #fff;
  cursor: pointer;
  transition: 0.3s;
}

.see-all {
  background-color: #fff;
  padding: 10px 20px;
  font-size: 24px;
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  border: 2px solid #fff;
  outline: none;
  border-radius: 5px;
  display: block;
  margin-inline: auto;
  margin-top: 30px;
}

.see-all a:visited,
.see-all a:active,
.see-all a:link {
  color: #000;
}

.see-all:hover a {
  color: navajowhite !important;
}

.see-all:hover {
  background-color: transparent;
  border: 2px solid #fff;
  cursor: pointer;
  transition: 0.3s;
}

.see-all a {
  text-decoration: none;
}

@media screen and (max-width: 1200px) {
  .projects-info {
    grid-template-columns: 450px 450px;
    grid-template-rows: auto;
    row-gap: 50px;
    column-gap: 50px;
  }

  .projects-info div img {
    height: 300px;
  }

  .overlay h2 {
    font-size: 34px;
  }

  .overlay p {
    font-size: 16px;
  }

  .overlay p span {
    font-size: 18px;
  }
}

@media screen and (max-width: 992px) {
  .projects-info {
    grid-template-columns: 320px 320px;
    row-gap: 40px;
    column-gap: 40px;
  }

  .projects-info div img {
    height: 200px;
  }

  .overlay h2 {
    font-size: 24px;
  }

  .overlay p {
    font-size: 15px;
  }

  .overlay p span {
    font-size: 17px;
  }

  .projects-btns button {
    font-size: 18px;
  }
}

@media screen and (max-width: 768px) {
  .projects {
    padding: 50px 0;
  }

  .projects-info {
    grid-template-columns: 450px;
  }

  .projects-info div img {
    height: 270px;
  }
}

@media screen and (max-width: 500px) {
  .projects-info {
    grid-template-columns: 350px;
  }

  .projects-info div img {
    height: 220px;
  }
}

@media screen and (max-width: 450px) {
  .projects h1 {
    font-size: 34px;
  }
}

@media screen and (max-width: 400px) {
  .projects {
    padding: 20px 0;
  }

  .projects-info {
    grid-template-columns: 230px;
  }

  .projects-info div img {
    height: 150px;
  }

  .overlay h2 {
    font-size: 20px;
  }

  .overlay p {
    font-size: 13px;
  }

  .overlay p span {
    font-size: 15px;
  }

  .projects-btns {
    width: 90%;
  }

  .projects-btns button {
    font-size: 15px;
    padding: 5px;
  }
}

@media screen and (max-width: 300px) {
  .projects h1 {
    font-size: 30px;
  }
}
