.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #1c1c1ce6;
  z-index: 100;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mobile-menu svg {
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 20px;
  margin-right: 20px;
  font-size: 45px;
  cursor: pointer;
}

.mobile-menu ul {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

.mobile-menu ul li {
  list-style: none;
  margin-bottom: 20px;
}

.mobile-menu ul li a {
  text-decoration: none;
  background-image: linear-gradient(180deg, #fff, #6c757d);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 900;
  padding: 10px 20px;
  font-family: "Poppins", sans-serif;
  font-size: 40px;
}

.mobile-menu ul li a:hover {
  border-left: 5px solid #fff;
}
