.skills {
  padding: 30px 0 100px 0;
}

.skills h1 {
  font-family: "Poppins", sans-serif;
  color: transparent;
  -webkit-text-stroke: 2px #e35d6a;
  font-size: 38px;
  font-weight: 900;
  letter-spacing: 2px;
  text-align: center;
  margin: 30px 0;
}

.skills-info {
  margin-inline: auto;
  display: grid;
  grid-template-columns: 200px 200px 200px;
  grid-template-rows: auto;
  justify-content: center;
  align-content: center;
  row-gap: 100px;
  -moz-column-gap: 50px;
  column-gap: 50px;
  background-color: #1c1c1c;
  padding: 30px 0;
  box-shadow: 0 0 0 100vmax #1c1c1c;
  -webkit-clip-path: inset(0 -100vmax);
  clip-path: inset(0 -100vmax);
}

.skills-info div {
  text-align: center;
}

.skills-info div svg {
  color: white;
  font-size: 130px;
}

.skills-info div svg:hover {
  cursor: pointer;
}

.html svg:hover {
  color: #dd4b25;
}

.css svg:hover {
  color: #254bdd;
}

.java-script svg:hover {
  color: #ead41c;
}

.react svg:hover {
  color: #5ed3f3;
}

.node-js svg:hover {
  color: #6aa05c;
}

.mysql svg:hover {
  color: #005e86;
}

.skills-info div p {
  color: white;
  font-size: 22px;
  margin-bottom: 0;
}

@media screen and (max-width: 1200px) {
  .skills {
    padding-bottom: 50px;
  }
}

@media screen and (max-width: 992px) {
  .skills {
    padding-bottom: 0px;
  }
}

@media screen and (max-width: 768px) {
  .skills-info {
    grid-template-columns: 200px 200px;
    row-gap: 50px;
  }
}

@media screen and (max-width: 500px) {
  .skills-info {
    grid-template-columns: 100px 100px;
    row-gap: 30px;
    -moz-column-gap: 30px;
    column-gap: 30px;
  }
  .skills-info div svg {
    font-size: 95px;
  }
  .skills-info div p {
    font-size: 20px;
  }
}

@media screen and (max-width: 450px) {
  .skills h1 {
    font-size: 34px;
  }
}

@media screen and (max-width: 300px) {
  .skills h1 {
    font-size: 30px;
  }
}
