.contact {
  padding: 100px 0;
  width: 80%;
  margin-inline: auto;
}

.contact h1 {
  font-family: "Poppins", sans-serif;
  color: transparent;
  -webkit-text-stroke: 2px #e35d6a;
  font-size: 38px;
  font-weight: 900;
  letter-spacing: 2px;
  text-align: center;
  margin: 30px 0 0 0;
  text-align: center;
}

.contact-title {
  color: #fff;
  text-align: center;
  font-size: 20px;
  margin-top: 10px;
  font-family: "Poppins", sans-serif;
  text-align: center;
}

.contact form div label {
  font-family: "Poppins", sans-serif;
  color: #fff;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 1px;
  display: inline-block;
  width: 150px !important;
}

.name input,
.email input {
  font-family: "Poppins", sans-serif;
  width: 350px;
  border: 2px solid #fff;
  outline: none;
  padding: 5px 10px;
  font-size: 20px;
  background-color: navajowhite;
  margin-top: 50px;
  border-radius: 5px;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  box-shadow: 0 0 0 30px navajowhite inset !important;
}

.message {
  margin: 50px auto;
  display: flex;
  align-items: flex-start;
}

.message textarea {
  width: 700px;
  height: 300px;
  font-family: "Poppins", sans-serif;
  border: 2px solid #fff;
  outline: none;
  padding: 5px 10px;
  font-size: 20px;
  background-color: navajowhite;
  border-radius: 5px;
}

.submit {
  background-color: #fff;
  padding: 10px 20px;
  font-size: 24px;
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  border: 2px solid #fff;
  outline: none;
  border-radius: 5px;
}

.submit:hover {
  background-color: transparent;
  color: navajowhite;
  border: 2px solid #fff;
  cursor: pointer;
  transition: 0.3s;
}

@media screen and (max-width: 1200px) {
  .contact {
    width: 90%;
    padding-top: 50px;
  }
}

@media screen and (max-width: 992px) {
  .contact {
    padding-top: 0px;
    padding-bottom: 70px;
  }

  .contact form div label {
    font-size: 20px;
  }

  .name input,
  .email input {
    width: 250px;
    font-size: 20px;
  }

  .message textarea {
    width: 500px;
    height: 250px;
  }
}

@media screen and (max-width: 768px) {
  .contact {
    width: 95%;
    text-align: center;
  }

  .contact form div label {
    font-size: 18px;
    width: 100px !important;
  }

  .message {
    justify-content: center;
  }

  .name input,
  .email input {
    width: 350px;
    font-size: 17px;
  }

  .message textarea {
    width: 350px;
    height: 200px;
    font-size: 17px;
  }

  .submit {
    padding: 5px 10px;
    font-size: 20px;
  }
}

@media screen and (max-width: 500px) {
  .contact {
    width: 95%;
    padding-top: 30px;
  }

  .contact-title {
    font-size: 18px;
  }

  .message,
  .name,
  .email {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .message {
    margin: 0 0 30px 0;
  }

  .contact form div label {
    font-size: 18px;
    width: 100px !important;
  }

  .name input,
  .email input {
    width: 80%;
    font-size: 14px;
    margin-top: 0;
    margin-bottom: 30px;
  }

  .message textarea {
    width: 80%;
    font-size: 14px;
  }
}

@media screen and (max-width: 450px) {
  .contact h1 {
    font-size: 34px;
  }
}

@media screen and (max-width: 300px) {
  .contact h1 {
    font-size: 30px;
  }
  .contact-title {
    font-size: 16px;
  }
}
